      scrollToCurrent(index) {
        this.selectedIndex = index+'';
        this.changeCurrentBanner();
      },

      debounce(func, delay) {
        let timer = null;

        return function(...args) {
          if (timer) {
            clearTimeout(timer);
          }

          timer = setTimeout(() => {
            func.apply(this, args);
          }, delay);
        }
      },
    }
  }
