<template>
  <div class="home-page">
    <top-banner></top-banner>
    <top-banner-bottom-news></top-banner-bottom-news>
    <home-live></home-live>
    <home-cute-moment-video></home-cute-moment-video>
    <home-favourite></home-favourite>
    <home-news></home-news>
    <home-subject></home-subject>
    <home-partner></home-partner>

    <back-top @click.native="backTopClick" v-show="isShowBackTop"></back-top>
  </div>
</template>

<script>
import BackTop from 'components/content/BackTop';

import TopBanner from './topbanner/TopBanner'
import TopBannerBottomNews from './topbanner/TopBannerBottomNews'
import HomeLive from './homeLive/HomeLive'
import HomeCuteMomentVideo from './HomeCuteMomentVideo'
import HomeFavourite from './HomeFavourite'
import HomeNews from './HomeNews'
import HomeSubject from './HomeSubject'
import HomePartner from './HomePartner'

  export default {
    components: {
      TopBanner,
      TopBannerBottomNews,
      HomeLive,
      HomeCuteMomentVideo,
      HomeFavourite,
      HomeNews,
      HomeSubject,
      HomePartner,
      BackTop
    },
    data() {
      return {
        isShowBackTop: false
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll, true);
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll); 
    },
    methods: {
      handleScroll() {
        var positionY = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        if (positionY > 380) {
          this.isShowBackTop = true;
        } else {
          this.isShowBackTop = false;
        }
      },
      backTopClick() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      }
    }
  }
</script>

